import React, { Component } from "react"
import MetaTags from 'react-meta-tags';

import { MDBDataTable } from "mdbreact"


import {
  Card,
  Col,
  Container,
  Row,
  CardBody,
  CardHeader,
  CardTitle,
  FormGroup,
  Label,
  Button,
  Spinner,
  Form,
  Input,
  InputGroupAddon,
  InputGroupText,
  InputGroup,
  Breadcrumb,
  BreadcrumbItem,
} from "reactstrap"
// import Select from "react-select"
// import axios from 'axios'
// import  { Redirect } from 'react-router-dom'
// // import $ from "jquery";

// import Flatpickr from "react-flatpickr"
// import ReactSelect from 'react-select'
// import makeAnimated from 'react-select/animated';
//import BootstrapSelect from "bootstrap-select"
import Confirmer from '../../../components/Confirmer.js'
import  { Redirect } from 'react-router-dom'
import conf from '../../../conf.js'
import { format } from 'date-fns';

class ListeTournoiEntreprise extends Component {


  constructor(props) {
    super(props)
    this.state = {
      tournoisData:[],
      id:0,
      show:false,
      tournois:[],
      page:<div style={{marginTop:"25%",marginBottom:"20%",textAlign:"center"}}>
      <Spinner type="grow" color="success" />
   </div>,
     status:true,
    }
    this.handleDetail.bind(this)
    this.supprimer.bind(this)
    this.handleModifier.bind(this)
   
  }
  componentDidMount() {
    let  data=  JSON.parse(localStorage.getItem('token_auth'))
    const requestOptions = {
      method: 'GET',
      headers: { 
        'Authorization':'Bearer '+data.token,
  
      },
    };

    fetch(conf.url+"tournoi/tournois",requestOptions)
    .then((response) => response.json())
    .then((data) => {
      console.log("data",data)
      this.setState({status:false})
     try{
      if(data.length){
        this.setState({tournoisData:data,status:false})

        // data.map((element,idx)=>{
        //  var tab= {
        //    id: element.id,
        //    nom: element.nom,
           
        //    prixparsa: element.prixPartSa,
        //    type:element.typeTournoi,
        //    annee: element.annee,
        //    datedebut: format(new Date(element.dateDebut),'dd/MM/yyyy'),
        //    datefin: format(new Date(element.dateFin),'dd/MM/yyyy'),
        
        //    action:  <a> <i style={{cursor:"pointer"}} onClick={this.handleModifier.bind(this,element.id)} className="bx bx-pencil bx-sm text-primary mr-2"/> 
        //               <i style={{cursor:"pointer"}} onClick={this.handleDetail.bind(this,element.id)} className="bx bx-info-circle bx-sm text-primary mr-2"/> 
        //               <i style={{cursor:"pointer"}} onClick={this.supprimer.bind(this,element.id)} className="bx bx-trash  bx-sm text-danger "/> 
        //             </a>,
           
        //  }
        //  //onClick={this.handleDetail.bind(this.bind,element.id)}
        
        //  this.setState({tournois:[...this.state.tournois,tab]})
   
        // })
      }else{
        this.setState({tournois:[]})
      }
     }catch(error){
      this.setState({tournois:[]})
     }
        
  
      },(error)=>{
        console.log(error)
      })
  }
  handleSupprimer=()=>{

    let  data=  JSON.parse(localStorage.getItem('token_auth'))
    const requestOptions = {
      method: 'GET',
      headers: { 
        'Authorization':'Bearer '+data.token,

      },
    };
    
  
  
  fetch(conf.url+"tournoi/tournois/delete/"+this.state.id, requestOptions)
  .then((response) => response.json())
    .then(data =>{console.log("enregitre avec succes vrai:",data)
     console.log("data send",data)
      this.setState({tournois:this.state.tournois.filter(element=>element.id!=this.state.id)})
      this.setState({show:false})
     
   } )
  }

  handleDetail=(id)=>{
    localStorage.setItem('idTournoi',id);
    this.setState({ok: <Redirect to='/detail-tournoi'/>});
  }
  toggled=()=>{
    this.setState({show:!this.state.show})
  }

  supprimer=(id)=>{
  
    this.setState({id:id,show:true})
    
  }

  handleModifier=(id)=>{
    localStorage.setItem('idTournoi',id);
    this.setState({ok: <Redirect to='/modifier-tournoi'/>});
    
  }

  render() {

    const data = {
      columns: [
          {
          label: "Année",
          field: "annee",
          sort: "asc",
          width: 100,
        },
        {
          label: "Nom",
          field: "nom",
          sort: "asc",
          width: 150,
        },
        {
          label: "Type de tournoi",
          field: "type",
          sort: "asc",
          width: 150,
        },
        // {
        //   label: "Année",
        //   field: "annee",
        //   sort: "asc",
        //   width: 100,
        // },
        // {
        //   label: "Description",
        //   field: "description",
        //   sort: "asc",
        //   width: 100,
        // },
       
        {
          label: "Prix",
          field: "prixparsa",
          sort: "asc",
          width: 100,
        },
        
        {
          label: "Date de début",
          field: "datedebut",
          sort: "asc",
          width: 100,
        },
        {
          label: "Date de fin",
          field: "datefin",
          sort: "asc",
          width: 100,
        },
        {
          label: "Action",
          field: "action",
          sort: "asc",
          width: 100,
        }
      ],
      rows:this.state.tournois
      
    }


    return (
      <React.Fragment>

        <div className="page-content"> 
          
        {this.state.ok}
        <Confirmer show={this.state.show} handleValider={this.handleSupprimer} toggle={this.toggled}/>

          <Container fluid={true}>
          {
          (!this.state.status)?

          (
            
            <Row>
              <Col md="12" className="pull-right">
                <Card>
                  <CardBody>
                    <Row ClassName="mb-4">
                      <Col lg="6" md="6" sm="12">
                        <div className="page-title-box d-flex align-items-center justify-content-between">
                          <h3 className="mb-0 font-size-18">Liste des tournois</h3>
                        </div>
                      </Col>

                      <Col lg="6" md="6" sm="12" className="pull-right">
                        <Button
                          color="success"
                          className="font-16 btn btn-success float-right"
                          href="/#/ajout-tournoi"
                        >
                          <i class='bx bx-plus-circle' > </i>  Ajouter un nouveau tournoi
                        </Button>
                      </Col>
                    </Row>

                    <hr/>

                    <Row ClassName="mt-5">
                    {  this.state.tournoisData.map((element,idx)=>(
                    <Col lg="6" md="6" sm="12" className="mt-2">
                    <Card className="block" id="block" style={{borderRadius:"25px",borderColor:"#4f8f3f",borderWidth:"5px",backgroundColor:"#FFFFF"}}>
                      <CardBody>
                        <CardTitle className="h4 mb-3">
                          <h3><span style={{backgroundColor:"#6A7172"}} class="badge rounded-pill ">{element.nom}</span></h3>
                        </CardTitle>
                        Nom: {element.nom}
                        <br/>
                        Type: {element.typeTournoi}
                        <br/>
                        Date début: {format(new Date(element.dateDebut),'dd/MM/yyyy')}
                        <br/>
                        
                        Date fin: {format(new Date(element.dateFin),'dd/MM/yyyy')}
                        <br/>
                       
                        Année: {element.annee}
                        <br/>
                        <div className="mt-3">
                          <button onClick={this.supprimer.bind(this,element.id)} type="button" style={{backgroundColor:"#4f8f3f",float:"right",cursor:"pointer",borderRadius:"25px"}} className="btn btn-info btn-elegant mr-2" >
                            <i className="fas fa-trash mr-1"></i>
                          </button>
                        
                          <button onClick={this.handleDetail.bind(this,element.id)} type="button" style={{backgroundColor:"#4f8f3f",float:"right",cursor:"pointer",borderRadius:"25px"}} className="btn btn-info btn-elegant mr-2" >
                            <i className="fas fa-info-circle mr-1"></i>
                          </button>
                          <button onClick={this.handleModifier.bind(this,element.id)} type="button" style={{backgroundColor:"#4f8f3f",float:"right",cursor:"pointer",borderRadius:"25px"}} className="btn btn-info btn-elegant mr-2" >
                            <i className="fas fa-pencil-alt mr-1"></i>
                          </button>
                          
                        </div>
                      </CardBody>
                   </Card>
                  </Col>
                  ))
                 }
                    </Row>

                  </CardBody>

                </Card>
              </Col>
            </Row>
              
              ):(<>{this.state.page}</>)
            }
          </Container>
        </div>
        
      </React.Fragment>
    )
  }
}
export default ListeTournoiEntreprise